.card {
  box-sizing: border-box;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 1rem;
  margin: 1rem auto;
  max-width: 614px;
  padding: 0.5rem;
  background-color: rgba(255, 255, 255, 0.03);
}
@media (max-width: 430px) {
  .card {
    padding: 0.5rem;
    border-radius: 0.8rem;
  }
}
